import React from "react";
import Carousel from 'react-bootstrap/Carousel';
import 'bootstrap/dist/css/bootstrap.min.css';

import testimonials1 from "../../images/testimonials-1.png";  // 
import testimonials2 from "../../images/testimonials-2.png";  // 
import testimonials3 from "../../images/testimonials-3.png";  //   
import testimonials4 from "../../images/testimonials-4.jpg";  // 
import testimonials5 from "../../images/testimonials-5.jpg";  // 
import testimonials6 from "../../images/testimonials-6.jpg";  // 
// import CarouselItem from "../CarouselItem/CarouselItem";
import CarouselSlideElem from "../CarouselSlideElem/CarouselSlideElem";
import "./Carousel.css";

const textComment11 = "  ,    .      .    . !"
const textComment22 = "    .         .   ,   - ."
const textComment33 = "       .   .       ."

function Carusel() {
  return (
    <section className="carusel">
      <main className="carusel__wrapper wrapper">
        <h2 className="carusel__title title general-title"></h2>
        <Carousel>
          <Carousel.Item>
            <CarouselSlideElem
              textComment1={textComment11}
              url1={testimonials1}
              subtitle1=" "
              textKind1=""
              textComment2={textComment22}
              url2={testimonials2}
              subtitle2=" "
              textKind2=" "
              textComment3={textComment33}
              url3={testimonials3}
              subtitle3=" "
              textKind3=""
            />
          </Carousel.Item>
          <Carousel.Item>
            <CarouselSlideElem
              textComment1={textComment11}
              url1={testimonials4}
              subtitle1=""
              textKind1=""
              textComment2={textComment22}
              url2={testimonials5}
              subtitle2=""
              textKind2=" "
              textComment3={textComment33}
              url3={testimonials6}
              subtitle3=""
              textKind3=""
            />
          </Carousel.Item>
          <Carousel.Item>
            <CarouselSlideElem
              textComment1={textComment11}
              url1={testimonials2}
              subtitle1=" "
              textKind1=""
              textComment2={textComment33}
              url2={testimonials6}
              subtitle2=""
              textKind2=" "
              textComment3={textComment33}
              url3={testimonials1}
              subtitle3=" "
              textKind3=""
            />
          </Carousel.Item>
        </Carousel>
      </main>
    </section>
  );
}

export default Carusel;
